import { LoginService } from './login.service';
import { Injectable } from '@angular/core';
import { HTTP_INTERCEPTORS, HttpEvent, HttpErrorResponse, HttpInterceptor, HttpHandler, HttpRequest } from '@angular/common/http';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { catchError, filter, switchMap, take } from 'rxjs/operators';
import { SignalRService } from './chat.service';

const TOKEN_HEADER_KEY = 'x-access-token';

@Injectable({
  providedIn: 'root'
})

export class ErrorInterceptor implements HttpInterceptor {
  constructor(private router: Router, private loginService: LoginService, private signalRService: SignalRService) { }

  private isRefreshing = false;
  private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    let token = this.loginService.getData('accessToken') || 'undefined';

    if (token !== 'undefined') {
      request = this.addToken(request, token);
    }

    return next.handle(request)
      .pipe(
        catchError(error => {
          if (error instanceof HttpErrorResponse && error.status === 401) {
            return this.handle401Error(request, next);
          } else {
            return throwError(error);
          }
        }
        )
      );
  }

  private addToken(request: HttpRequest<any>, token: string) {
    return request.clone({
      setHeaders: {
        'Authorization': `Bearer ${token}`
      }
    });

    // request = request.clone({
    //   setHeaders: { Authorization: `Bearer ${token}` }
    // });
  }

  private handle401Error(request: HttpRequest<any>, next: HttpHandler) {
    if (!this.isRefreshing) {
      this.isRefreshing = true;
      this.refreshTokenSubject.next(null);
      let obj = {
        userID: sessionStorage['userID'],
        refreshToken: sessionStorage['refreshToken']
      }
      console.log("Refresh Token Request", obj);

      return this.loginService.refreshToken(obj).pipe(
        switchMap((token: any) => {
          console.log("Refresh Token Response", token);

          this.isRefreshing = false;
          if (token.result === 'Success') {
            this.loginService.setData('accessToken', token.accessToken)
            this.refreshTokenSubject.next(token.accessToken);
            return next.handle(this.addToken(request, token.accessToken));
          }
          else {
            this.signalRService.removeFromGroup()
            this.loginService.redirect();
            return throwError(token.message);
          }
        }),
        catchError((err) => {
          this.isRefreshing = false;
          this.signalRService.removeFromGroup()
          this.loginService.redirect();
          return throwError(err);
        })
      );
    }

    return this.refreshTokenSubject.pipe(
      filter(token => token != null),
      take(1),
      switchMap(token => {
        console.log("Refresh Token 1", token);
        return next.handle(this.addToken(request, token));
      }));
  }

  private logOut() {

    var obj = {
      customerID: localStorage['customerID'],
      userID: localStorage['userID']
    }
    this.loginService.logout(obj).subscribe((res) => {
      this.signalRService.removeFromGroup()
      this.loginService.redirect();
    }, (err) => {
      console.log(err)
    })
  }
}