import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { API_URL } from '../constants/constants';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { EMPTY, of } from "rxjs";

@Injectable({
  providedIn: 'root'
})

export class LoginService {

  constructor(private http: HttpClient, private router: Router) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  login(user: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Login/LoginUser', user, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  refreshToken(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Login/RefreshToken', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  registerClient(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/DialService/RegisterClient', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  registerSupervisor(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/DialService/RegisterSupervisor', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  getSecretQuestion(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Login/GetSecretQuestion', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  verifySecretAnswer(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Login/verifySecretAnswer', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    );
  }

  logout(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Login/LogoutUser', data, { headers: this.headers }).pipe(
      retry(1),
      catchError(this.handleError)
    )
  }

  redirect() {
    localStorage.clear();
    this.router.navigate(['/']);
  }
  
  setSession(key: any, val: any): void {
    sessionStorage[key] = val;
  }

  setData(key: any, val: any): void {
    sessionStorage[key] = val;
  }

  getData(key: any): any {
    return sessionStorage[key];
  }


  gotoNotFound() {
    this.router.navigate(['/403']);
  }

  isAuthenticated(): boolean {
    return sessionStorage.userID != null;
  }

  handleError(error: any) {
    return throwError(error.message || error);
  }

}
