import { Injectable } from '@angular/core';
import * as signalR from "@aspnet/signalr"; // or from "@microsoft/signalr" if you are using a new library
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { API_URL } from '../constants/constants';
import { retry, catchError } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class SignalRService {
  
  constructor(private http: HttpClient) { }

  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });

  public data!: any[];
  public connectionId: any;
  public hubConnection!: signalR.HubConnection
  public groupName: any;

  

  startConnection = () => {
    this.groupName = JSON.parse(sessionStorage['user']).userName;
    //this.groupName = sessionStorage['userID'];
    this.hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(API_URL + '/chatsocket', {
        skipNegotiation: true,
        transport: signalR.HttpTransportType.WebSockets
      })
      .build();
    
    this.hubConnection
      .start()
      .then(() => console.log('Connection started'))
      .then(() => this.getConnectionId())
      .then(() => this.addToGroup(this.groupName))
      .catch((err: string) => console.log( err, API_URL + '/chatsocket'))
  }

  addToGroup = (groupName: any) => {
    console.log(sessionStorage.userID)
    this.hubConnection.invoke('AddToGroup', groupName).then(() => {
      console.log("AddToGroup", groupName)})
      .catch((err: string) => console.log("AddToGroup Error", err))
  }

  //Called Directly from the Page.ts
/*   public receiveManagerMessageListener = () => {
    this.hubConnection.on('ReceiveManagerMessage', (data: any[]) => {
      this.data = data;
      console.log(data);
    });
  } */

  getConnectionId = () => {
    this.hubConnection.invoke('getconnectionid').then(
      (data) => {
        console.log("ConnectionId" , data);
      })
  }

  removeFromGroup = () => {
    if (this.hubConnection) {  
      console.log("RemoveFromALLGroup")
       this.hubConnection.invoke('RemoveFromALLGroup', JSON.parse(localStorage['user']).userName)
        .catch((err: string) => console.log("RemoveAllFromGroup Error", err))
    }
  }

  getAllAgent(): Observable<any> {
    return this.http.get<any>(API_URL + '/api/Agent/GetAllAgents', { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getAllManagers(): Observable<any> {
    return this.http.get<any>(API_URL + '/api/Users/GetAllManagers', { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  sendChatMessage(data: any): Observable<any> {
    return this.http.post<any>(API_URL + '/api/Chat/SendChatMessage', data, { headers: this.headers })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  getImage(name: string): Observable<any> {
    return this.http.get(API_URL + '/api/FileUpload/GetImage/' + name, { responseType: "blob" })
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  handleError(error: any) {
    return throwError(error.message || error);
  }
}